    .image {
        display: inline-grid;
        width: 100%;
        height: 100%;
        border-top-right-radius: 30px;
        border-bottom-right-radius: 30px;
    }


@media (max-width: 745px) {
    .image {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
}

@media (max-width: 376px) {
    .image {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
}